(function main() {
  document.addEventListener('DOMContentLoaded', () => {
    /* eslint-disable no-unused-vars */
    const headerElement = document.getElementById('header');
    const mainElement = document.getElementById('main');
    const footerElement = document.getElementById('footer');
    const navButtonElement = document.querySelector('header button.hamburger');
    /* eslint-enable no-unused-vars */

    const initialHeaderHeight = window.getComputedStyle(headerElement).getPropertyValue('height');

    /**
     * Toggles a blog navigation tree element.
     * @param event {object} - The event object of the element that is used to toggle.
     */
    function toggleTreeElement(event) {
      const listItem = event.currentTarget.parentElement;
      const subList = listItem.nextElementSibling;
      const state = listItem.classList.toggle('active');

      if (subList) {
        subList.style.height = `${(state ? subList.scrollHeight : 0)}px`;
      }

      event.preventDefault();
      event.stopPropagation();
    }

    /**
     * Toggles the main navigation of the page on small devices.
     * @param event {object} - The event object of the element that is used to toggle.
     */
    function toggleNavigation(event) {
      const state = navButtonElement.classList.toggle('active');

      headerElement.style.height = state ? `${headerElement.scrollHeight}px` : initialHeaderHeight;

      event.preventDefault();
      event.stopPropagation();
    }

    /**
     * Closes the main navigation of the page on small devices.
     */
    function closeNavigation() {
      navButtonElement.classList.remove('active');
      headerElement.style.height = initialHeaderHeight;
    }

    // Assign functions to global space
    window.toggleTreeElement = toggleTreeElement;
    window.toggleNavigation = toggleNavigation;

    window.onresize = closeNavigation;
  });
}());
